import { useState,useEffect} from 'react'
import { getBlogsDateData } from '../../../actions/admin'
import { getCookie } from '../../../actions/auth'
import {Line} from 'react-chartjs-2';

export default () => {
  const [data, setData] = useState([])
  const [chartData,setChartData] = useState([])
  const token = getCookie('token')
  useEffect(() => {
    getBlogsDateData(token).then(res => { 
      if (res.error) { 
        return console.log(res.error)
      }
      setData(res.data)
      setChartData(data.sort((a,b) => (a._id.day > b._id.day) ? 1 : ((b._id.day > a._id.day) ? -1 : 0)).map(({_id},i)=>_id.day))
    })
  }, [])
      console.log(data)

  return (
      <Line data={{
  labels: data.sort((a,b) => (a._id.day > b._id.day) ? 1 : ((b._id.day > a._id.day) ? -1 : 0)).map(({_id},i)=>_id.day),
  datasets: [
    {
      label: 'Blogs dataset',
      fill: false,
      lineTension: 0.1,
      backgroundColor: 'rgba(75,192,192,0.4)',
      borderColor: 'rgba(75,192,192,1)',
      borderCapStyle: 'butt',
      borderDash: [],
      borderDashOffset: 0.0,
      borderJoinStyle: 'miter',
      pointBorderColor: 'rgba(75,192,192,1)',
      pointBackgroundColor: '#fff',
      pointBorderWidth: 1,
      pointHoverRadius: 5,
      pointHoverBackgroundColor: 'rgba(75,192,192,1)',
      pointHoverBorderColor: 'rgba(220,220,220,1)',
      pointHoverBorderWidth: 2,
      pointRadius: 1,
      pointHitRadius: 10,
      data: data.sort((a,b) => (a._id.day > b.last_nom) ? 1 : ((b._id.day > a._id.day) ? -1 : 0)).map(({count},i)=>count)
    }
  ]
}} />
  )
};